<template>
  <div class="item-preview" ref="itemPreview">
    <div class="generate-options">
      <div class="d-md-flex align-items-md-center justify-content-md-center">
        <div class="form-inline mr-md-1">
          <label class="my-1 mx-md-1" for="itemName">
            <strong>Nome do artefato:</strong>
          </label>
          <input
            class="form-control mx-md-1 my-1"
            type="text"
            id="itemName"
            v-model="itemName"
          />
        </div>
        <button
          class="btn text-left construct-artifact"
          :class="constructButtonClasses"
          type="button"
          @click="generateImage"
          :disabled="calculatingZoom"
        >
          <i class="ra ra-fairy-wand mr-2"></i>
          <strong v-if="item.uuid">Reconstruir artefato</strong>
          <strong v-else>Criar artefato</strong>
        </button>
      </div>
    </div>
    <div class="editor-center">
      <img
        class="loading"
        :hidden="!calculatingZoom"
        src="../../public/assets/loading.png"
        alt="Calculando preview..."
      />
      <div
        class="item-cards"
        :style="{zoom: previewZoom}"
        ref="itemCards"
        :hidden="calculatingZoom"
      >
        <ItemCard>
          <BackgroundCard :item="item" />
        </ItemCard>
        <ItemCard>
          <FrontCard :item="item" />
        </ItemCard>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex';
  import { mapFields } from 'vuex-map-fields';

  import BackgroundCard from './BackgroundCard.vue';
  import FrontCard from './FrontCard.vue';
  import ItemCard from './ItemCard.vue';

  export default {
    name: 'ItemPreview',
    components: {
      BackgroundCard,
      FrontCard,
      ItemCard
    },
    data () {
      return {
        calculatingZoom: true,
        calculatingZoomTimeout: null,
        scale: 'md',
        previewZoom: 1
      }
    },
    computed: {
      ...mapState([ 'item' ]),
      ...mapFields({
        itemName: 'item.name'
      }),
      constructButtonClasses() {
        return {
          'btn-secondary': !this.item.uuid,
          'btn-success': !!this.item.uuid
        }
      },
      previewSize () {
        return `item-preview-${this.scale}`;
      }
    },
    mounted () {
      this.$nextTick(function() {
        window.addEventListener('resize', this.updateZoom);

        this.updateZoom();
      });
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.updateZoom);
    },
    methods: {
      ...mapActions([ 'setGeneratedItem' ]),
      async exportItem () {
        const el = this.$refs.itemCards;

        return await this.$html2canvas(el);
      },
      async generateImage () {
        const canvas = await this.exportItem();

        this.setGeneratedItem({
          name: `${this.item.name}.jpg`,
          url: canvas.toDataURL('image/jpeg'),
          width: canvas.width,
          height: canvas.height
        });
      },
      updateZoom () {
        this.calculatingZoom = true;

        if (this.calculatingZoomTimeout) {
          clearTimeout(this.calculatingZoomTimeout);
        }

        this.calculatingZoomTimeout = setTimeout(() => {
          const cardsHeight = 1039;

          const { offsetHeight, offsetWidth } = this.$refs.itemPreview;

          const availableSize = Math.min(offsetHeight, offsetWidth) * 0.8;

          const zoom = parseFloat((availableSize / cardsHeight).toFixed(2));

          this.previewZoom = Math.min(zoom, 1);

          this.calculatingZoom = false;
        }, 500);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .item-preview {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    width: 100%;

    @media (max-width: 576px) {
      height: auto;
      max-height: auto;
    }

    .editor-center {
      align-items: center;
      display: flex;
      justify-content: center;
      flex: 1 1 auto;

      @media (max-width: 576px) {
        height: 100vh !important;
      }
    }

    $transition: transform 1s ease-in-out;

    $maxWidth: 1500px;

    .item-cards {
      display: flex;
      max-width: $maxWidth;
      overflow: hidden;
      position: relative;
      width: $maxWidth;

      @media (max-width: 576px) {
        zoom: 0.25 !important;
      }
    }

    .generate-options {
      background-color: white;
      height: auto;
      left: 0;
      padding: .125rem 1rem;
      top: 0;
      width: 100%;

      @media (max-width: 576px) {
        background-color: transparent;
        position: relative;
      }
    }

    @keyframes spin {
      from {
        transform:rotate(0deg);
      }
      to {
        transform:rotate(360deg);
      }
    }

    .loading {
      animation-name: spin;
      height: 20em;
      width: 20em;
      animation-duration: 5000ms;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
  }
</style>