<template>
  <div class="export-import-items">
    <button
      class="btn btn-secondary mb-0 mr-2"
      type="button"
      @click="exportGeneratedItems"
      :disabled="!hasGeneratedItems"
      title="Exportar configuraes de artefatos para arquivo .json"
    >
      <i class="fa fa-cloud-download mr-1"></i> Exportar
    </button>
    <label
      class="btn btn-secondary mb-0"
      for="importItems"
      title="Importar configuraes de artefatos de arquivo .json"
    >
      <i class="fa fa-cloud-upload mr-1"></i> Importar
    </label>
    <input
      class="d-none m-0"
      type="file"
      id="importItems"
      @change="importItems($event.target)"
      :multiple="false"
      lang="pt-br"
      accept=".json"
      hidden
    />
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {
    name: 'ExportImportItems',
    computed: {
      ...mapGetters([ 'hasGeneratedItems' ])
    },
    methods: {
      ...mapActions([
        'exportGeneratedItems',
        'importGeneratedItems'
      ]),
      importItems ({ files }) {
        this.importGeneratedItems(files[0]);
      }
    }
  }
</script>

<style lang="scss" scoped>
  abbr {
    border-bottom: none !important;
    cursor: inherit !important;
    text-decoration: none !important;
  }
</style>